$primero: rgb(236, 180, 59);
$segundo: linear-gradient(225deg, hsl(41deg 83% 79%) 0%, hsl(41deg 82% 60%) 20%, hsl(41deg 82% 55%) 35%, hsl(41deg 82% 50%) 100%);
$tercero: linear-gradient(225deg, hsl(41deg 83% 79%) 0%, hsl(41deg 82% 60%) 30%, hsl(41deg 82% 65%) 60%, hsl(41deg 82% 50%) 100%);

$cuarto: linear-gradient(225deg, rgb(255, 255, 255) 0%, rgb(255, 254, 251) 20%, hsl(41deg 82% 55%) 35%, hsl(41deg 82% 50%) 100%);

$quinto: rgb(236, 180, 59);
$sexto: linear-gradient(225deg, hsl(41deg 83% 79%) 0%, hsl(41deg 82% 60%) 80%, hsl(41deg 82% 65%) 95%, hsl(41deg 82% 50%) 100%);
$thumbColor: rgb(234, 14, 14);
$colorText: rgb(81, 79, 79);
// $displayWidth: 100vw;
// $displayMargin: auto;

body {
  background-image: linear-gradient(358deg, hsl(41deg 82% 50%) 0%, hsl(41deg 82% 55%) 30%, hsl(41deg 82% 60%) 50%, hsl(41deg 83% 79%) 65%, hsl(41deg 83% 86%) 70%, hsl(0deg 0% 100%) 75%, hsl(0deg 0% 100%) 100%);

  box-sizing: border-box;
}

#calculator {
  margin: 3vw auto; // indent from window borders
  background-color: white;
  border-radius: 25px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px, rgba(0, 0, 0, 0.3) 0px 30px 60px -30px, rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}

#displayBox {
  background-image: linear-gradient(358deg, hsl(0deg 0% 100%) 0%, hsl(0deg 0% 100%) 10%, hsl(41deg 83% 86%) 15%, hsl(41deg 83% 79%) 20%, hsl(41deg 82% 60%) 30%, hsl(41deg 82% 55%) 70%, hsl(41deg 82% 50%) 100%);
  border-radius: 25px 25px 0 0;
}

#display {
  height: 13vw; // height of the result display
  padding-top: 4.3vw; // indent from top border of parent container
  text-align: center; // styling result
  font-weight: 800; // styling result
  font-size: 6.4vw; // styling result
  color: white; // styling result
}

#displayAll {
  height: 9rem; // height of the displayOps
  padding-top: 4.3vw; // indent from top border of parent container
  font-weight: 600; // styling displayOps
  font-size: 4.3vw; // styling displayOps
  color: white; // styling displayOps
}

#button-pad {
  background-color: white;
}

button {
  background: none;
  border: none;
  margin: 0.5vw;
  width: 13vw;
  height: 13vw;
  box-shadow: 2px 2px rgba(110, 86, 38, 0.438);
  border-radius: 15px;
  font-weight: 800;
  font-size: 4.5vw;
  overflow: hidden; // prevents text overflowing it's container
}

.main-btn {
  background-image: $segundo;
  color: white;
}

.secondary-btn {
  color: $primero;
  font-weight: 600;
  text-shadow: -0.5px 0 black, 0 0.5px black, 0.5px 0 black, 0 -0.5px black;
}

.tertiary-btn {
  color: $primero;
  font-weight: 600;
  text-shadow: -0.5px 0 black, 0 0.5px black, 0.5px 0 black, 0 -0.5px black;
}

.digits-btn {
  background-image: $tercero;
  color: white;
}

#memorySlot {
  // background-image: linear-gradient(358deg, hsl(41deg 82% 50%) 0%, hsl(41deg 82% 55%) 30%, hsl(41deg 82% 60%) 70%, hsl(41deg 83% 79%) 80%, hsl(41deg 83% 86%) 85%, hsl(0deg 0% 100%) 90%, hsl(0deg 0% 100%) 0%);
  // background-color: white;
  border-radius: 0 0 25px 25px;
  padding-top: 10vw;
  padding-bottom: 10vw;
  justify-content: center;
  justify-items: auto;
  margin: auto;
}

.ms-btn {
  margin-top: 2vw;
  background-image: $segundo;
  width: 6vw;
  height: 6vw;
  display: inline;
  color: white;
  font-size: 4vw;
}

.ms-text {
  width: 52vw;
  height: 8vw;
  display: inline-block;
  border: 1px solid $primero;
  border-radius: 5px;
  font-size: 1rem;
  color: $primero;
  font-weight: 600;
  text-shadow: -0.5px 0 black, 0 0.5px black, 0.5px 0 black, 0 -0.5px black;
  text-align: center;
}

[parentheses-delta] {
  position: relative;
}
[parentheses-delta]::before {
  content: attr(parentheses-delta);
  position: absolute;
  top: 1.2vw;
  right: 1.2vw;
  width: 5vw;
  height: 5vw;
  font-size: 4.5vw;
  font-weight: 700;
  display: flex;
  justify-content: center;
  align-items: center;
  color: red;
}

footer {
  margin-top: 5vw;
  font-size: 3vw;
  font-style: italic;
  color: white;
  font-weight: 600;
  text-align: center;
}
